body {
  font-family: sans-serif;
}

#form {
  border: 1px solid black;
  display: grid;
  place-content: center;
  gap: 0.1rem;

  padding: 1rem;
  margin-bottom: 1rem;
}

.input-width {
  display: inline-block;

  margin-bottom: 1rem;
}

.input-depth {
  display: inline-block;
}

#startBtn {
  display: inline-block;
  padding: 0.1rem 1rem;
  margin: auto;

  font-size: 1.2rem;
}

#resultTable {
  display: grid;
  place-content: center;

  gap: 0.1rem;
}

#nameRow {
  border-bottom: 1px solid black;
}

th {
  background-color: hsla(0deg, 0%, 0%, 0.2);
  padding: 0.2rem 0.5rem;

  text-justify: center;
}

th:first-of-type {
  background-color: hsla(0deg, 0%, 0%, 0.1);
}

td {
  background-color: hsla(0deg, 0%, 0%, 0.05);
  padding: 0.2rem 0.5rem 0.2rem 0.5rem;

  text-justify: center;
}
